import React, { useState, useEffect } from 'react';
import './App.css';
import { Container, Toolbar, AppBar, Button, Avatar } from '@material-ui/core';
import { makeStyles } from '@material-ui/core/styles';
import Recognition from './components/Recognition';
import Footer from './components/Footer';
import Entries from './components/Entries';
import UserInfo from './components/UserInfo';
import loginService from './services/login';
import entryService from './services/entries';
import { Switch, Route, Link } from 'react-router-dom';
import { Switch as ToggleMode } from '@material-ui/core';
import { ThemeProvider, createMuiTheme } from '@material-ui/core/styles';
import { deepOrange } from '@material-ui/core/colors';
import { Alert, AlertTitle } from '@material-ui/lab';
import Pagination from '@material-ui/lab/Pagination';

const useStyles = makeStyles((theme) => ({
  root: {
    flexGrow: 1,
  },
  rooted: {
    width: '100%',
    '& > * + *': {
      marginTop: theme.spacing(2),
    },
  },
  orange: {
    color: theme.palette.getContrastText(deepOrange[500]),
    backgroundColor: deepOrange[500],
  },
  pagination: {
    alignItems: 'center',
    textAlign: 'center',
    justifyContent: 'center',
    justifySelf: 'center',
  },
  paginator: {
    alignItems: 'center',
    justifyItems: 'center',
  },
}));

const App = () => {
  let [entries, setEntries] = useState([]);
  const [darkMode, setDarkMode] = useState(false);
  const [notification, setNotification] = useState(null);
  const [user, setUser] = useState(null);
  const [page, setPage] = useState(1);
  const [limit, setLimit] = useState(4);

  useEffect(() => {
    entryService
      .getAll(page, limit)
      .then((entries) => setEntries(entries.results));
  }, [page, limit]);

  const handleChange = (event, value) => {
    setPage(value);
  };

  const theme = createMuiTheme({
    palette: {
      type: darkMode ? 'dark' : 'light',
    },
  });

  useEffect(() => {
    const loggedUserJSON = window.localStorage.getItem('loggedBlogAppUser');
    if (loggedUserJSON) {
      const user = JSON.parse(loggedUserJSON);
      setUser(user);
      entryService.setToken(user.token);
    }
  }, []);

  const logOut = () => {
    window.localStorage.removeItem('loggedBlogAppUser');
    setUser(null);
  };

  const handleLogin = async ({ username, password }) => {
    try {
      const user = await loginService.login({ username, password });
      window.localStorage.setItem('loggedBlogAppUser', JSON.stringify(user));
      entryService.setToken(user.token);
      setUser(user);

      setNotification(`Welcome ${user.name}!`);

      setTimeout(() => {
        setNotification(null);
      }, 5000);
    } catch (error) {
      console.log(error);
    }
  };

  const addEntry = (entryObject) => {
    entryService
      .create(entryObject)
      .then((returnedObject) => {
        setEntries([...entries, returnedObject]);
        setNotification(`New entry ${returnedObject.title}!`);
        setTimeout(() => {
          setNotification(null);
        }, 5000);
      })
      .catch((err) => {
        console.log(err);
      });
  };

  const handleLikes = (id) => {
    const entry = entries.find((entry) => entry.id === id);
    const likedEntry = { ...entry, likes: entry.likes + 1 };
    entryService.update(id, likedEntry).then(() => {
      setEntries(
        entries.map((entry) => (entry.id !== id ? entry : likedEntry))
      );
    });
  };

  const handleDeletion = (id) => {
    const entryToBeDeleted = entries.find((blog) => blog.id === id);
    if (window.confirm(`Remove ${entryToBeDeleted.title}?`)) {
      entryService.deleted(id).then(() => {
        setEntries(entries.filter((blog) => blog !== entryToBeDeleted));
      });
    }
  };

  const classes = useStyles();

  return (
    <ThemeProvider theme={theme}>
      <Container>
        <div className={classes.rooted}>
          {notification && (
            <Alert variant="filled" severity="success">
              <AlertTitle>{notification}</AlertTitle>
            </Alert>
          )}
        </div>
        <AppBar position="static">
          <Toolbar>
            <Button color="inherit" component={Link} to="/">
              home
            </Button>
            <Button color="inherit" component={Link} to="/entries">
              entries
            </Button>
            <Button
              className={classes.loginbtn}
              color="inherit"
              component={Link}
              to="/login"
            >
              {user ? (
                <Avatar className={classes.orange}>
                  {user.name.substring(0, 2)}
                </Avatar>
              ) : (
                'login/sign-up'
              )}
            </Button>
            <ToggleMode
              checked={darkMode}
              onChange={() => setDarkMode(!darkMode)}
            />
          </Toolbar>
        </AppBar>
        <Switch>
          <Route path="/entries">
            <Entries
              user={user}
              entries={entries}
              handleLikes={handleLikes}
              handleDeletion={handleDeletion}
            />
            {user ? (
              <div className={classes.pagination}>
                <Pagination
                  className={classes.paginator}
                  align="center"
                  count={10}
                  page={page}
                  onChange={handleChange}
                  defaultPage={1}
                  color="primary"
                  size="large"
                  showFirstButton
                  showLastButton
                />
              </div>
            ) : (
              ''
            )}
          </Route>
          <Route path="/login">
            <UserInfo user={user} logOut={logOut} handleLogin={handleLogin} />
          </Route>
          <Route path="/">
            <Recognition user={user} createEntry={addEntry} />
          </Route>
        </Switch>
        <Footer />
      </Container>
    </ThemeProvider>
  );
};

export default App;
